import React from 'react';
import requisitos from '../data/requisitos.json';

const RequisitosServicio = ({ tramiteId }) => {
  const tramite = requisitos.tramites.find(t => t.id === tramiteId);

  if (!tramite) return null;

  return (
    <div className="mt-6 p-4 bg-gray-50 rounded-lg">
      <h3 className="text-lg font-semibold mb-4">Requisitos para {tramite.nombre}</h3>
      <ul className="list-disc list-inside space-y-2 text-gray-700">
        {tramite.requisitos.map((requisito, index) => (
          <li key={index} className="text-sm">
            {requisito}
          </li>
        ))}
      </ul>
      
      {tramite.notas && tramite.notas.length > 0 && (
        <div className="mt-4 pt-4 border-t border-gray-200">
          <h4 className="text-sm font-semibold text-gray-700 mb-2">Información Importante:</h4>
          <ul className="list-none space-y-1">
            {tramite.notas.map((nota, index) => (
              <li key={index} className="text-sm text-gray-600 flex items-start">
                <span className="text-blue-500 mr-2">•</span>
                {nota}
              </li>
            ))}
          </ul>
        </div>
      )}
    </div>
  );
};

export default RequisitosServicio;
